<template>
  <div class="my">
    <div class="my-bg" :style="{backgroundImage: `url(${userInfo.userBg})`}">
      <van-uploader v-if="isLogin" class="uploader-box" :after-read="upload" />
    </div>
    <div class="my-content">
      <div class="my-boxing">
        <div v-if="isLogin" class="my-box">
        <div class="my-box">
          <div class="my-img">
            <div class="img-box">
              <img class="auto-img" :src="userInfo.userImg" alt />
            </div>
            <div class="my-info">
              <div class="nickname">{{userInfo.nickName}}</div>
              <div class="desc">{{userInfo.desc == '' ? '这个家伙很懒，什么都没留下' : userInfo.desc}}</div>
            </div>
          </div>
        </div>
         </div>
       <div class="not-login" v-else>
          <span @click="goLogin">请先登录</span>
        </div>

        <div class="sanlan">
          <div class="kdu">
            <h2 class="shuliang">0</h2>
            <p class="wenzi">粉丝</p>
          </div>
          <div class="kdu">
            <h2 class="shuliang">0</h2>
            <p class="wenzi">动态</p>
          </div>
          <div class="kdu">
            <h2 class="shuliang">0</h2>
            <p class="wenzi">M币</p>
          </div>
        </div>
      </div>
    </div>

    <div class="lists">
      <van-cell
        :title="item.title"
        :icon="item.icon"
        :to="item.to"
        size="large"
        is-link
        v-for="(item, index) in lists"
        :key="index"
      
      />
     
    </div>

   
  </div>
</template>

<script>
import "../../assets/scss/my.scss";
import { utils } from "../../assets/js/utils";

export default {
  data() {
    return {
     
      lists: [
        { title: "个人设置", icon: require("../../assets/images/grxx.png"),
          to: { name: "Account" } },
        { title: "我的消息", icon: require("../../assets/images/wdxx.png"),
         to: { name: "Message" }},
        { title: "我的月卡", icon: require("../../assets/images/wdyk.png") },
        { title: "私房商城", icon: require("../../assets/images/sfsc.png") },
        { title: "偏好设置", icon: require("../../assets/images/phsz.png"),  to: { name: "Secure" }},
      ],
      isLogin: false,
      userInfo: {},
    };
  },

  created() {
     this.getUserInfo();
  },

  methods: {
    goLogin() {
        this.$router.push({ name: "Login" });
    },

    //查询用户信息
    getUserInfo() {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "GET",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "A001") {
            if (result.data.result.length > 0) {
              this.isLogin = true;
              this.userInfo = result.data.result[0];
              window.localStorage.setItem("nickName", JSON.stringify(result.data.result[0].nickName));
             
            }
          }
        })
          this.$toast.clear();
         
      
    },

    //上传背景
     upload(file) {
      //获取token字符串
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return;
      }

      //获取文件类型
      let type = file.file.type.split("/")[1];
      let imgTypes = ["png", "gif", "jpg", "jpeg"];
      if (imgTypes.indexOf(type) === -1) {
        this.$notify({
          message: "图片类型只支持" + imgTypes.join(","),
          color: "#fff",
          background: "#49dda1"
        });

        return;
      }

      //大小
      if (this.maxSize < file.file.size) {
        this.$notify({
          message: "上传文件大小不能超过" + this.maxSize / 1024 + "KB",
          color: "#fff",
          background: "#49dda1"
        });
        return;
      }

      // 

      //获取base64
      let base64 = file.content.replace(/data:image\/[A-Za-z]+;base64,/, "");

      //参数序列化
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        imgType: type,
        serverBase64Img: base64
      });

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner"
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == "I001") {
            this.userInfo.userBg = result.data.userBg;
          }
        })
          this.$toast.clear();
    },
   
  },
};
</script>
